.Portfolio {
    width: 100%;
    height: auto;
    padding-top: 80px;
    position: relative;
    .title {
        font-size: 48px;
        color: rgba(192, 192, 194, 1);
        font-family: 'Josefin Sans', sans-serif;
        text-align: start;
    }
    .portfolioImg{
        width: 100%;
        height: 60vh;
        color: white;
        background: rgba(192, 192, 194, 1);
        border: 1px solid white;
    }
}
@media only screen and (max-width:992px){
    .portfolioImg{
        margin: 20px 0 20px 0;
    }
}